import React from 'react';
import "./endzone-combo-box.css"

class EndzoneComboBox extends React.Component {
    constructor (props) {
        super(props);
    }
    render() {
        return(
            <div className='endzone-text-input-div' style={this.props.style}>
                <label className='endzone-text-label'>{this.props.label}</label>
                <select className='endzone-comboBox' id = {this.props.id}>
                    {this.props.options.map((item, index) => <option value={item.value}>{item.text}</option>)}
                </select>
            </div>
        );
    }
}



export default EndzoneComboBox;